const periodsObjects = [{ value: 1, label: "1st Period" }, { value: 2, label: "2nd Period" }, { value: 3, label: "3rd Period" }, { value: 4, label: "4th Period" }, { value: 5, label: "5th Period" }, { value: 6, label: "6th Period" }, { value: 7, label: "7th Period" }, { value: 8, label: "8th Period" }, { value: 9, label: "9th Period" }, { value: 10, label: "10th Lunch Break" }]

class AttendanceUI extends React.Component {
    constructor(props) {
        super(props);
        const { t } = this.props;
        this.fieldVariables = {
            periodDetails: []
        }
        this.state = {
            periodObj: "", periodStartTime: "07:30", periodEndTime: "08:30", successMessage: '', isSuccess: false, students: [], errorMessage: '', isError: false
        };
    }

    backDashboard = () => {
        this.setState({ isError: false, isSuccess: false })
    }
    backTimeTable = () => {
        this.setState({ isSuccess: false })
        this.props.history.push(`./create-period`);
    }

    setPeriodValue = (value) => {
        let periodObj
        periodsObjects.map((item) => {
            if (item.value === value) {
                periodObj = item
            }
        })
        return periodObj;
    }
    handlePeriodChange = (event) => {
        this.setState({ periodObj: this.setPeriodValue(event.value) })
    }
    handleStartTimeChange = (event) => {
        this.setState({ periodStartTime: event.target.value })
    }
    handleEndTimeChange = (event) => {
        this.setState({ periodEndTime: event.target.value })
    }
    handelSubmit = async () => {
        if (this.state.periodObj.value && this.state.periodStartTime && this.state.periodEndTime) {
            let dataToSend = {
                periodId: this.state.periodObj.value,
                periodStartTime: this.state.periodStartTime,
                periodEndTime: this.state.periodEndTime
            }
            let response = await this.props.authenticatedApiCall('post', "/api/timetableservice/createperiods", dataToSend);
            if (response.data.status === 1) {
                this.setState({ isSuccess: true, successMessage: response.data.statusDescription });
            } else {
                this.setState({ isError: true, successMessage: response.data.statusDescription });
            }
        } else {
            this.setState({ isError: true, errorMessage: "First fill all the fileds." });
        }
    }
    handleCancel = () => {
        this.props.history.push(`./timetable`);
    }
    handleChangeCheckBox = () =>{
        this.props.handleCheckBox(this.props.studentId, this.props.classid, this.props.section)
    }
    render() {
        const { classes } = this.props;
        return (
            <Formik onSubmit={this.handleSubmit} validationSchema={this.yupSchema} initialValues={{ present:true }}>
                {(props) => (
                    <Form>
                        <Field
                            name="present"
                            component={SimpleCheckBox}
                            value="checkbox"
                            // checked= {true}
                            onClick={this.handleChangeCheckBox}
                        />
                    </Form>
                )}
            </Formik>
        );
    }
}
export default withStyles(styles)(AuthenticatedPage("Principal")(WithAccount(AttendanceUI)));